@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600&family=Raleway:wght@200;300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&family=Ubuntu:wght@300;400;500&display=swap);
header {
  background-color: #16443ef6;
  padding: 50px 0px 40px;
  position: fixed;
  top: 0px;
  right: 0;
  width: 100%;
  z-index: 99;
}

.nav-item {
  padding: 0px 10px;
}

.nav-item a {
  font-weight: 500;
  font-size: 20px;
  color: rgb(255, 255, 255);
}

.navbar-expand-lg .navbar-nav .dropdown-menu {
  position: absolute;
  text-align: center;
  /* background-color: #16443eda !important; */
  background-color: rgba(66, 64, 60, 0.301);
  font-family: inherit;
  border: none;
  margin: 0;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #0c0a0a;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-item.active {
  transition: all linear 0.3s;
  color: wheat;
  text-decoration: none;
  background-color: #707479dc !important;
}

.dropdown-item {
  transition: all linear 0.3s;
}

.dropdown-item:hover {
  color: wheat;
  text-decoration: none;
  background-color: #70747983 !important;
}

.nav-link {
  border-bottom: 2px solid transparent;
  transition: all linear 0.3s;
}

.nav-link:hover {
  cursor: pointer;
  color: #c5d7bd;
  border-bottom: 2px solid #8d6a6a;
}

.navbar-brand {
  margin-right: 0 !important;
  right: 50%;
  -webkit-transform: translate(50%, -60px);
  transform: translate(50%, -60px);
  top: 0;
  position: absolute;
}

.navbar-brand img {
  max-width: 180px;
  transition: all linear 0.3s;
}

.rotate {
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

.rotate:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.rotating {
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

@media all and (min-width: 992px) {
  .navbar .nav-item .dropdown-menu {
    display: none;
  }
  .navbar .nav-item:hover .dropdown-menu {
    display: block;
  }
  .navbar .nav-item .dropdown-menu {
    margin-top: 0;
  }
}

@media (max-width: 767px) {
  .navbar-expand-lg .navbar-nav .dropdown-menu {
    position: absolute;
    text-align: center;
    background-color: #16443eda !important;
  }
  @media (max-width: 767px) {
     
  .logo {
    width: 100%;
  }
  .logo img {
    width: 30%;
    margin: auto;
  }
  .header-title {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 30px;
  }
  }
}

/* Universal CSS */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  font-family: Ubuntu;
  background: #e7e7e7;
  color: #e4e4e4;
  overflow-x: hidden;
  font-weight: 400;
  background-color: #ffd2da94;
}

body::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
  background: rgb(61, 61, 59); /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(158, 158, 175); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 3px solid rgb(145, 145, 151); /* creates padding around scroll thumb */
}

/* Universal CSS */

h2 {
  color: #0c253a;
  font-size: 26px;
  font-weight: 400;
}

h4 {
  color: #e0245e;
}

h5 {
  color: #093d68;
}

.card {
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 50px;
  border-radius: 20px;
  background: linear-gradient(300deg, #ffbed781, #ebabff85);
  box-shadow: 14px 10px 13px 0px #d8c5c5;
  margin-bottom: 70px;
  border: none;
}
.btn {
  margin-top: 30px;
  padding: 10px 20px;
  border: 2px solid #17bf63;
  background-color: transparent;
  border-radius: 25px;
  transition: all linear 0.5s;
}

.btn a {
  font-family: Ubuntu;
  color: #17bf63;
  text-decoration: none;
  transition: all linear 0.5s;
  font-weight: 400;
}

.btn:hover {
  background-color: #17bf63;
  border: 2px solid transparent;
}

.btn:hover a {
  color: #15202b;
}

@media (max-width: 599px) {
  .btn {
    margin-top: 0px;
    padding: 7px 15px;
    background-color: transparent;
    border-radius: 25px;
    transition: all linear 0.5s;
  }
}

